@import 'susy';
@import url('https://fonts.googleapis.com/css?family=Open+Sans|Source+Sans+Pro');
@font-face {
    font-family: 'sansationbold';
    src: url('../fonts/Sansation-Bold-webfont.eot');
    src: url('../fonts/Sansation-Bold-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/Sansation-Bold-webfont.woff') format('woff'),
         url('../fonts/Sansation-Bold-webfont.ttf') format('truetype'),
         url('../fonts/Sansation-Bold-webfont.svg#sansationbold') format('svg');
    font-weight: normal;
    font-style: normal;
}
@import 'variables';
@import 'extends';
@import 'mixins';
@import 'global';
@import 'layout';
//@import 'layoutXXL';
//@import 'layoutXL';
@import 'layoutL';
//@import 'layoutM';
@import 'layoutS';
@import 'layoutXS';

$susy: (
  columns: 12,
  gutters: 1/4,
  math: fluid,
  output: float,
  gutter-position: inside,
);

